import React from "react";
import styles from "./Work.module.scss";
//import images
import img1 from "../assets/work1/1.jpg";
import img2 from "../assets/work2/2.jpg";
import img3 from "../assets/work3/3.jpg";
import img4 from "../assets/work4/1.jpg";
import img5 from "../assets/work5/1.jpg";
import img6 from "../assets/work6/1.jpg";
import img7 from "../assets/work7/1.jpg";

import { motion } from "framer-motion";

const workAnimation = {
  hidden: { y: 20, opacity: 0 },
  visible: (custom) => ({
    y: 0,
    opacity: 1,
    transition: {
      delay: custom * 0.2,
    },
  }),
};

const Work = () => {
  const scrollRef = React.useRef(null);

  return (
    <section className={styles["work"]} id="section1">
      <motion.div
        ref={scrollRef}
        initial="hidden"
        whileInView="visible"
        viewport={{ once: true }}
        className="container"
      >
        <h2 className={styles["work__title"]}>Nasze prace</h2>
        <motion.div className={styles["card-container"]}>
          <motion.div
            custom={1}
            whileHover={{ scale: 1.01 }}
            variants={workAnimation}
            className={styles.card}
          >
            <img src={img1} alt="img" />
            <div className={styles.content}>
              <p>
                Przedstawiamy Pastwu projekt naszych klientw , zrealizowany
                przez nasz zesp . Pan Piotr wraz z on zaprojektowali sobie
                sopelki z balustrad a my zrobilimy reszt <span></span>
                koszyki na doniczki te z naszej rki <span></span>
              </p>
            </div>
          </motion.div>
          <motion.div
            custom={2}
            whileHover={{ scale: 1.01 }}
            variants={workAnimation}
            className={styles.card}
          >
            <img src={img2} alt="img" />
            <div className={styles.content}>
              <p>
                Kolejna , ciekawa nasza praca <span></span> Balustrada z
                nietypowym pochwytem z rury stalowej . Cikie byy te zakrty ,
                ale jak wida na zdjciu dalimy rad <span></span>
              </p>
            </div>
          </motion.div>
          <motion.div
            custom={3}
            whileHover={{ scale: 1.01 }}
            variants={workAnimation}
            className={styles.card}
          >
            <img src={img3} alt="img" />
            <div className={styles.content}>
              <p>
                Skrojone na miar  Sople  z balustrad wkomponoway si
                idealnie <span></span>
              </p>
            </div>
          </motion.div>
          <motion.div
            custom={4}
            whileHover={{ scale: 1.01 }}
            variants={workAnimation}
            className={styles.card}
          >
            <img src={img4} alt="img" />
            <div className={styles.content}>
              <p>
                Schody ju byy , ale brakowao balustrad , a wic zjawilimy
                si i efekt wida <span></span>
              </p>
            </div>
          </motion.div>
          <motion.div
            custom={5}
            whileHover={{ scale: 1.01 }}
            variants={workAnimation}
            className={styles.card}
          >
            <img src={img5} alt="img" />
            <div className={styles.content}>
              <p>
                Kolejny klient zadowolony. Jest nam z tego powodu bardzo mio i
                cieszymy si z realizacji jego wizji -piknej barierki
                wewntrznej.
              </p>
            </div>
          </motion.div>
          <motion.div
            custom={6}
            whileHover={{ scale: 1.01 }}
            variants={workAnimation}
            className={styles.card}
          >
            <img src={img6} alt="img" />
            <div className={styles.content}>
              <p>
                Przedstawiamy kolejn realizacje barierki ze stali czarnej
                malowanej proszkowo na kolor czarny mat . Wypenienie z rurek
                25 z projektu klienta . Wszyscy zadowoleni z efektu
                przeprowadzonych prac .
              </p>
            </div>
          </motion.div>
          <motion.div
            custom={7}
            whileHover={{ scale: 1.01 }}
            variants={workAnimation}
            className={styles.card}
          >
            <img src={img7} alt="img" />
            <div className={styles.content}>
              <p>
                Idealnie dopasowana balustrada <span></span> Klient zadowolony
                , my rwnie
                <span></span> Dzikujemy za zaufanie <span></span>
              </p>
            </div>
          </motion.div>
        </motion.div>
      </motion.div>
    </section>
  );
};

export default Work;
